@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
body {
  height: 100vh;
  box-sizing: border-box;
  background: linear-gradient(45deg, rgb(194, 225, 218), rgb(43, 143, 146))
    no-repeat;
  font-family: "Roboto", sans-serif;
}
body::-webkit-scrollbar {
  width: 0;
}
img {
  display: block;
}
h1 {
  text-align: center;
}
.container {
  padding: 5rem;
  width: 800px;
  margin: 0 auto;
}
.card {
  border-radius: 6px;
  background: linear-gradient(45deg, rgb(194, 225, 218), rgb(43, 143, 146));
  padding: 1rem;
  box-shadow: 14px 18px 17px 0px rgba(0, 0, 0, 0.4);
}
.btn {
  color: #2b8f92;
  position: relative;
  border-radius: 99px;
  border: 1px solid #2b8f92;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  font-weight: 700;
  outline: none;
  background: #fff;
  transition: all 0.22s;
  cursor: pointer;
  margin-right: 1rem;
}
.btn.primary {
  background: #2b8f92;
  color: #fff;
}
.btn:active {
  box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.3);
}
.btn:hover {
  cursor: pointer;
  opacity: 0.8;
}
input[type="file"] {
  display: none;
}

.preview {
  display: flex;
  flex-wrap: wrap;
  padding: 0.5rem;
}
.preview-img {
  position: relative;
  margin-bottom: 0.5rem;
  margin-right: 0.5rem;
  overflow: hidden;
  height: 100%;
}

.preview-img.removing {
  transform: scale(0);
  transition: transform 0.3s;
}

.preview-img img {
  width: 180px;
  height: auto;
}
.preview-remove {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
  font-weight: bold;
  font-size: 20px;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.2s;
}
.preview-img:hover .preview-remove {
  opacity: 1;
}
.preview-img:hover .preview-info {
  bottom: 0px;
}
.preview-info {
  right: 0;
  height: 25px;
  bottom: -30px;
  position: absolute;
  left: 0;
  font-weight: bold;
  font-size: 0.8rem;
  background: rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
  transition: bottom 0.2s;
  overflow: hidden;
}
.preview-info-progress {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background: #42b983;
  text-align: center;
  transition: width 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
